import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import axios from "axios";

import changeTitle from "../utils/change-title";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Select from "../components/FormElements/Select";
import Input from "../components/FormElements/Input";
import H4 from "../components/Headings/H4";
import SideBySide from "../components/UI/SideBySide";
import DataTable from "../components/Tables/DataTable";
import Loader from "../components/UI/Loader";
import isEmpty from "../utils/is-empty";

const AdminOrder = () => {
  const { orderId } = useParams();
  const [orderIsLoading, setOrderIsLoading] = useState(true);
  const [orderState, setorderState] = useState({});
  const [sesssionsListIsLoading, setSesssionsListIsLoading] = useState(true);
  const [sessionsListState, setSessionsListState] = useState([]);
  const [selectedSessionState, setSelectedSessionState] = useState("");
  const [sessionBeingUpdate, setSessionBeingUpdate] = useState(false);
  const [contactInfoState, setcontactInfoState] = useState({
    ContactID: "",
    ContactEmail: "",
    ContactPhone: "",
  });
  const [contactInfoBeingUpdate, setContactInfoBeingUpdate] = useState(false);
  const [supportCategoryIsLoading, setSupportCategoryIsLoading] = useState(
    true
  );
  const [supportCategoryState, setSupportCategoryState] = useState([]);
  const [supportState, setSupportState] = useState({
    SupportCategoryID: "",
    OrderID: orderId,
    Name: "",
    Email: "",
    Phone: "",
  });
  const [supportBeingUpdate, setSupportBeingUpdate] = useState(false);
  const [supportListIsLoading, setsupportListIsLoading] = useState(true);
  const [supportListState, setSupportListState] = useState([]);
  const [ticketLogIsLoading, setTicketLogIsLoading] = useState(true);
  const [ticketLogState, setTicketLogState] = useState([]);
  const [ticketLeftIsLoading, setTicketLeftIsLoading] = useState(true);
  const [ticketLeftState, setTicketLeftState] = useState([]);
  const [attendeeIsLoading, setAttendeeIsLoading] = useState(true);
  const [attendeeState, setAttendeeState] = useState([]);
  const [orderEmailIsLoading, setOrderEmailIsLoading] = useState(true);
  const [orderEmailState, setOrderEmailState] = useState([]);
  const [orderSMSIsLoading, setOrderSMSIsLoading] = useState(true);
  const [orderSMSState, setOrderSMSState] = useState([]);
  const [attendeeInput, setAttendeeInput] = useState({
    OrderID: orderId,
    Name: "",
  });

  const sessionChangeHandler = (e) => {
    setSelectedSessionState(e.target.value);
  };

  const sessionUpdateHandler = (e) => {
    e.preventDefault();

    setSessionBeingUpdate(true);
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .post(
        "/updateordersession",
        {
          OrderID: orderId,
          SessionID: selectedSessionState,
        },
        { cancelToken: source.token }
      )
      .finally(() => {
        setSessionBeingUpdate(false);
        source.cancel();
      });
  };

  const contactInfoChangeHandler = (e) => {
    const { name, value } = e.target;

    setcontactInfoState((prevState) => {
      return { ...prevState, [name]: value };
    });
  };

  const contactInfoUpdateHandler = (e) => {
    e.preventDefault();

    setContactInfoBeingUpdate(true);
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .post("/updateordercontact", contactInfoState, {
        cancelToken: source.token,
      })
      .then(() => {})
      .finally(() => {
        source.cancel();
        setContactInfoBeingUpdate(false);
      });
  };

  const supportChangeHandler = (e) => {
    setSupportState((prevState) => {
      return { ...prevState, SupportCategoryID: e.target.value };
    });
  };

  const supportUpdateHandler = (e) => {
    e.preventDefault();
    setSupportBeingUpdate(true);

    axios.post("/createsupport", supportState).finally(() => {
      setSupportBeingUpdate(false);
    });
  };

  const attendeeInputChangeHandler = (e) => {
    setAttendeeInput((prevState) => {
      return { ...prevState, Name: e.target.value };
    });
  };

  const addAttendeeHandler = (e) => {
    e.preventDefault();
    setAttendeeIsLoading(true);
    console.log(attendeeInput);
    axios
      .post("/addattendee", attendeeInput)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setAttendeeState([...attendeeState, convertedRes]);
      })
      .finally(() => {
        setAttendeeInput((prevState) => {
          return { ...prevState, Name: "" };
        });
        setAttendeeIsLoading(false);
      });
  };

  useEffect(() => {
    changeTitle("Admin Order");
  }, []);

  useState(() => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getorder/${orderId}`, { cancelToken: source.token })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setorderState(convertedRes);
        setcontactInfoState({
          ContactID: convertedRes.ContactID,
          ContactEmail: convertedRes.ContactEmail,
          ContactPhone: convertedRes.ContactPhone,
        });
        setSupportState({
          ...supportState,
          Name: convertedRes.ContactName,
          Email: convertedRes.ContactEmail,
          Phone: convertedRes.ContactPhone,
        });
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        source.cancel();
        window.scrollTo(0, 0);
        setOrderIsLoading(false);
      });
  }, []);

  useEffect(() => {
    if (!isEmpty(orderState)) {
      const CancelToken = axios.CancelToken;
      const source = CancelToken.source();
      axios
        .post(
          "/geteventsessionslist",
          { EventID: orderState.EventID, SessionHideYN: false },
          { cancelToken: source.token }
        )
        .then((res) => {
          const convertedRes = JSON.parse(res.data);
          setSessionsListState(convertedRes);
        })
        .finally(() => {
          setSesssionsListIsLoading(false);
          source.cancel();
        });
    }
  }, [orderState]);

  useEffect(() => {
    setAttendeeIsLoading(true);
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getattendeelist/${orderId}`, {
        cancelToken: source.token,
      })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        const mappedResponse = convertedRes.map((el) => {
          return {
            AttendeeID: el.AttendeeID,
            Name: el.Name,
          };
        });
        setAttendeeState(mappedResponse);
      })
      .finally(() => {
        setAttendeeIsLoading(false);
        source.cancel();
      });
  }, [orderId]);

  useEffect(() => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get("/getsupportcategory", {
        cancelToken: source.token,
      })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setSupportCategoryState(convertedRes);
      })
      .finally(() => {
        source.cancel();
        setSupportCategoryIsLoading(false);
      });
  }, []);

  useEffect(() => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getordersupportlist/${orderId}`, { cancelToken: source.token })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setSupportListState(convertedRes);
        console.log(convertedRes);
      })
      .finally(() => {
        setsupportListIsLoading(false);
        source.cancel();
      });
  }, [orderId]);

  useEffect(() => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getticketlog/${orderId}`, { cancelToken: source.token })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setTicketLogState(convertedRes);
        console.log(convertedRes);
      })
      .finally(() => {
        setTicketLogIsLoading(false);
        source.cancel();
      });
  }, [orderId]);

  useEffect(() => {
    axios
      .get(`/getticketleft/${orderId}`)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setTicketLeftState(convertedRes);
        console.log(convertedRes);
      })
      .finally(() => {
        setTicketLeftIsLoading(false);
      });
  }, [orderId]);

  useEffect(() => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getticketleft/${orderId}`, { cancelToken: source.token })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setTicketLeftState(convertedRes);
        console.log(convertedRes);
      })
      .finally(() => {
        setTicketLeftIsLoading(false);
        source.cancel();
      });
  }, [orderId]);

  useEffect(() => {
    axios
      .get(`getorderemail/${orderId}`)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setOrderEmailState(convertedRes);
      })
      .finally(() => {
        setOrderEmailIsLoading(false);
      });
  }, [orderId]);

  useEffect(() => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    axios
      .get(`/getordersms/${orderId}`, { cancelToken: source.token })
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setOrderSMSState(convertedRes);
        console.log(convertedRes);
      })
      .finally(() => {
        setOrderSMSIsLoading(false);
        source.cancel();
      });
  }, [orderId]);

  return (
    <>
      {orderIsLoading ? (
        <Loader />
      ) : (
        <section>
          <SideBySide left="Order No:" right={orderState.OrderNo} />
          <SideBySide
            left="Date:"
            right={new Date(orderState.OrderDate).toLocaleString()}
          />
          <SideBySide left="Order Status:" right={orderState.OrderDate} />
          <SideBySide left="Name:" right={orderState.ContactName} />
          <SideBySide left="Email:" right={orderState.ContactEmail} />
          <SideBySide left="Phone:" right={orderState.ContactPhone} />
        </section>
      )}

      <hr />

      <section>
        <H4>Personal Details</H4>

        {orderIsLoading ? (
          <Loader />
        ) : (
          <div className="basic-form">
            <form onSubmit={contactInfoUpdateHandler}>
              <FormGroup label="Email *">
                <Input
                  type="name"
                  placeholder="Enter Email"
                  name="ContactEmail"
                  onChange={contactInfoChangeHandler}
                  value={contactInfoState.ContactEmail || ""}
                />
              </FormGroup>

              <FormGroup label="Phone *">
                <Input
                  type="name"
                  placeholder="Enter Phone"
                  name="ContactPhone"
                  onChange={contactInfoChangeHandler}
                  value={contactInfoState.ContactPhone || ""}
                />
              </FormGroup>

              <FormGroup>
                <FormButton disabled={contactInfoBeingUpdate}>
                  Update Info
                </FormButton>
              </FormGroup>
            </form>
          </div>
        )}
      </section>

      <hr />

      <section>
        <H4>Session Details</H4>

        {sesssionsListIsLoading ? (
          <Loader />
        ) : (
          <div className="basic-form">
            <form onSubmit={sessionUpdateHandler}>
              <FormGroup>
                <Select
                  value={selectedSessionState}
                  onChange={sessionChangeHandler}
                  firstEmpty
                  options={sessionsListState.map((el) => {
                    return {
                      text: `${new Date(
                        el.SessionStartDateTime
                      ).toLocaleString()} - ${new Date(
                        el.SessionEndDateTime
                      ).toLocaleString()}`,
                      value: el.SessionID,
                    };
                  })}
                ></Select>
              </FormGroup>

              <FormGroup>
                <FormButton disabled={sessionBeingUpdate}>
                  Update Session
                </FormButton>
              </FormGroup>
            </form>
          </div>
        )}
      </section>

      <section>
        <H4>Payment Details</H4>

        <div className="basic-form">
          <form>
            <div className="form-group mb-0">
              <label className="radio-inline mr-3">
                <input type="radio" name="optradio" /> Full Refund
              </label>
              <label className="radio-inline mr-3">
                <input type="radio" name="optradio" /> Partial Refund
              </label>
              <label className="radio-inline mr-3">
                <input type="radio" name="optradio" /> Minus Booking fee
              </label>
            </div>

            <FormGroup>
              <FormButton>Update Info</FormButton>
            </FormGroup>
          </form>
        </div>
      </section>

      <hr />

      <section>
        {orderSMSIsLoading ? (
          <Loader />
        ) : (
          <DataTable heading="SMS Details" titles={["Date", "SMS"]}>
            {orderSMSState.map((el, index) => {
              return (
                <tr key={el.SMSLogID}>
                  <td>{index + 1}</td>
                  <td>{new Date(el.AddedDate).toLocaleString()}</td>
                  <td dangerouslySetInnerHTML={{ __html: el.Message }}></td>
                </tr>
              );
            })}
          </DataTable>
        )}

        <SideBySide left="Bitly URL:" right="Not created" />
        <FormGroup>
          <FormButton className="mt-5">Generate</FormButton>
        </FormGroup>
        <FormGroup>
          <Input textarea rows="4" placeholder="Enter message here..." />
        </FormGroup>
        <FormGroup>
          <FormButton>Send</FormButton>
        </FormGroup>
      </section>

      <hr />

      <section>
        {orderEmailIsLoading ? (
          <Loader />
        ) : (
          <DataTable
            heading="Email Details"
            titles={["Date", "Subject", "Email"]}
          >
            {orderEmailState.map((el, index) => {
              return (
                <tr key={index} style={{ verticalAlign: "sub" }}>
                  <td>{index + 1}</td>
                  <td>{new Date(el.AddedDate).toLocaleString()}</td>
                  <td>{el.Subject}</td>
                  <td dangerouslySetInnerHTML={{ __html: el.EmailBody }}></td>
                </tr>
              );
            })}
          </DataTable>
        )}
      </section>

      <hr />

      <section>
        <H4>Support Details</H4>

        {supportCategoryIsLoading ? (
          <Loader />
        ) : (
          <div className="basic-form">
            <form onSubmit={supportUpdateHandler}>
              <FormGroup>
                <Select
                  onChange={supportChangeHandler}
                  value={supportState.SupportCategoryID}
                  options={supportCategoryState.map((el) => {
                    return {
                      text: el.Name,
                      value: el.SupportCategoryID,
                    };
                  })}
                />
              </FormGroup>

              <FormGroup>
                <FormButton disabled={supportBeingUpdate}>
                  Create Support
                </FormButton>
              </FormGroup>
            </form>
          </div>
        )}
      </section>

      <hr />

      {supportListIsLoading ? (
        <Loader />
      ) : (
        <section>
          <DataTable
            heading="Support List"
            titles={["Name", "Email", "Phone", "Action"]}
          >
            {supportListState.map((el, index) => {
              return (
                <tr key={index}>
                  <td>{index + 1}</td>
                  <td>{el.Name}</td>
                  <td>{el.Email}</td>
                  <td>{el.Phone}</td>
                  <td>
                    <Link
                      to={`/ticket-support/${el.SupportID}`}
                      className="btn btn-primary"
                    >
                      View
                    </Link>
                  </td>
                </tr>
              );
            })}
          </DataTable>
        </section>
      )}

      <hr />

      <section>
        {ticketLeftIsLoading ? (
          <Loader />
        ) : (
          <DataTable
            heading="Tickets Left"
            titles={["Name", "Total Quantity", "Left Quantity"]}
          >
            {ticketLeftState.map((el, index) => {
              return (
                <tr key={index}>
                  <td>{index + 1}</td>
                  <td>{el.EventItemName}</td>
                  <td>{el.TotalQty}</td>
                  <td>{el.LeftQty}</td>
                </tr>
              );
            })}
          </DataTable>
        )}
      </section>

      <hr />

      <section>
        {ticketLogIsLoading ? (
          <Loader />
        ) : (
          <DataTable
            heading="Tickets Logged"
            titles={["Name", "Log Date", "Log Quantity"]}
          >
            {ticketLogState.map((el, index) => {
              return (
                <tr key={index}>
                  <td>{index + 1}</td>
                  <td>{el.EventItemName}</td>
                  <td>{new Date(el.TicketLogDate).toLocaleString()}</td>
                  <td>{el.LogQty}</td>
                </tr>
              );
            })}
          </DataTable>
        )}
      </section>

      <section>
        {attendeeIsLoading ? (
          <Loader />
        ) : (
          <DataTable heading="Attendees" titles={["Attendee"]}>
            {attendeeState.map((el, index) => {
              return (
                <tr key={index}>
                  <td>{index + 1}</td>
                  <td>{el.Name}</td>
                </tr>
              );
            })}
          </DataTable>
        )}

        <div className="basic-form">
          <form onSubmit={addAttendeeHandler}>
            <FormGroup label="Companion Name *">
              <Input
                type="text"
                name="Name"
                placeholder="Enter Name"
                value={attendeeInput.Name}
                onChange={attendeeInputChangeHandler}
              />
            </FormGroup>

            <FormGroup>
              <FormButton>Add Companion</FormButton>
            </FormGroup>
          </form>
        </div>
      </section>
    </>
  );
};

export default AdminOrder;
